let columns = [
    {
      "title": "日期", 	//标题文字
      "prop": "date", //标题key，处理表格下载数据用到,折线图表用到
      "customTitle": "", //title + customTitle = 表格th标题
      "keysArray": ["date"], //当前列展示的数据key值，可以一个展示多个值,处理表格下载数据用到
      "icon": "el-icon-time", // 图标icon
      "fixed": true
    },

    {
      "title": "整合收入",
      "prop": "integrateChargeMoney",
      "customTitle": "",
      "keysArray": ["integrateChargeMoney"],
      // "tooltipTitle": "整合充电桩收入",
      "isSortable": true
    },

    {
      "title": "自营收入",
      "prop": "selfChargeMoney",
      "customTitle": "",
      "keysArray": ["selfChargeMoney"],
      "tooltipTitle": "自营充电桩收入",
      "isSortable": true
    },

    {
      "title": "自营收入",
      "prop": "selfChargeMoney",
      "customTitle": "",
      "keysArray": ["selfChargeMoney"],
      "tooltipTitle": "自营充电桩收入",
      "isSortable": true
    },
    
    {
      "title": "自营收入",
      "prop": "selfChargeMoney",
      "customTitle": "",
      "keysArray": ["selfChargeMoney"],
      "tooltipTitle": "自营充电桩收入",
      "isSortable": true
    },    

  ];

  export default columns


